*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-size: normal;
}

ul {
  list-style-type: none;
}

img, video, embed, object {
  max-width: 100%;
}

img {
  vertical-align: middle;
}

label, button {
  cursor: pointer;
}

.clearfix::before, .clearfix::after, .grid::before, .grid::after {
  content: "";
  display: table;
}

.clearfix::after, .grid::after {
  clear: both;
}

.ma {
  margin-left: auto;
  margin-right: auto;
}

.site-width {
  max-width: 75em;
}

html, body {
  background: #FFF;
  color: #000;
}

body, input, select, textarea, button {
  font-family: "Open Sans", sans-serif;
  font-size: 100%;
}

.page-nav {
  background: #15263e;
  color: #FFF;
  text-align: center;
  padding: 1.5em;
}

.page-nav__li {
  display: inline-block;
  position: relative;
}

.page-nav__li::after {
  content: "-";
  position: absolute;
  top: 50%;
  right: -0.15em;
  transform: translate(0, -50%);
}

.page-nav__li:last-child::after {
  display: none;
}

.page-nav__item {
  display: block;
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
  transition: opacity 0.2 ease;
  font-size: 1.125em;
  padding: 1em 2em;
}

.page-nav__item:hover {
  opacity: 0.7;
}

.section {
  margin-bottom: 3em;
}

.section__title {
  text-transform: uppercase;
  color: #15263e;
  font-weight: bold;
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 3.125em;
}

.section__intro-text {
  color: #4d4d4d;
  text-align: center;
  font-size: 1.125em;
  margin-bottom: 4rem;
}

.page-footer {
  background: #dcdcde;
  padding: 6.25em 1.5em;
}

.footer-module {
  margin-bottom: 2em;
  font-size: 0.875em;
}

.footer-module:last-child {
  margin-bottom: 0;
}

.footer-module__icon, .footer-module__content {
  display: inline-block;
  vertical-align: top;
}

.footer-module__icon {
  background: #374362;
  width: 5.4375rem;
  height: 5.4375rem;
  border-radius: 100%;
  position: relative;
  font-size: 2.8125em;
  color: #FFF;
}

.footer-module__icon .fa {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.footer-module__title {
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 1em;
  font-size: 1em;
}

.footer-module__content {
  color: #4d4d4d;
  margin-left: 2em;
}

.footer-module__content a {
  color: inherit;
  text-decoration: none;
}

.footer-module__content a:hover {
  text-decoration: underline;
}

