.welcome {
  background: #15263e url(../images/welcome_background.jpg) no-repeat center center;
  background-size: cover;
  position: relative;
  height: 46.25em;
}

.welcome__title {
  font-size: 7.375rem;
  text-transform: uppercase;
  font-weight: 300;
  color: #FFF;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.75);
  line-height: 1;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.welcome__title-complement {
  font-size: 2.25rem;
  display: block;
  font-weight: 600;
}

.services__item {
  text-align: center;
  margin-bottom: 4em;
  font-size: 1.125em;
}

.services__item-icon {
  font-size: 2.8125em;
  display: inline-block;
  width: 7.1875rem;
  height: 7.1875rem;
  background: linear-gradient(45deg, #b119ec, #f8027e);
  border-radius: 100%;
  margin-bottom: 1.3rem;
  position: relative;
  color: #FFF;
}

.services__item-icon .fa {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.services__item-title {
  font-size: 1.5625em;
  font-weight: bold;
  margin-bottom: 1.3rem;
  text-transform: uppercase;
  color: #4d4d4d;
}

.services__item-description {
  color: #494949;
  line-height: 1.9;
}

.portfolio__nav {
  text-align: left;
  margin-bottom: 2em;
}

.portfolio__nav-item {
  color: #4d4d4d;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 600;
  transition: color .2s ease;
  display: block;
  font-size: 1.375em;
  margin-bottom: 1.25em;
}

.portfolio__nav-item:hover, .portfolio__nav-item.is-active {
  color: #a91aef;
}

.portfolio__item {
  display: block;
  margin-bottom: 2em;
  text-decoration: none;
  border: 3px solid transparent;
  transition: border .2s ease;
}

.portfolio__item:hover {
  border-color: #a91aef;
}

.portfolio__item img {
  max-width: none;
  width: 100%;
}
